import React from "react";
import { Link } from "react-router-dom";

import PropTypes from "prop-types";
import clsx from "clsx";
import { withStyles } from "@material-ui/core/styles";
import Divider from "@material-ui/core/Divider";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import PublicIcon from "@material-ui/icons/Public";
import TimerIcon from "@material-ui/icons/Timer";
import SettingsIcon from "@material-ui/icons/Settings";

const categories = [
  {
    id: "Global Fire Map",
    children: [
      /*{ id: 'World', icon: <PublicIcon />, active: true },*/
      { id: "Canada", icon: <PublicIcon />, active: false, path: "/canada" },
      { id: "Alaska", icon: <PublicIcon />, active: false , path: "/alaska"},
      { id: "USA (Conterminous) and Hawaii", icon: <PublicIcon />, active: false , path: "/usa-hawaii"},
      { id: "Central America", icon: <PublicIcon />, active: false , path: "/central-america"},
      { id: "Europe", icon: <PublicIcon />, active: false , path: "/europe"},
      { id: "North and Central Africa", icon: <PublicIcon />, active: false , path: "/north-and-central-africa"},
      { id: "Southern Africa", icon: <PublicIcon />, active: false , path: "/southern-africa"},
      { id: "Russia and Asia", icon: <PublicIcon />, active: false , path: "/russia-and-asia"},
      { id: "South Asia", icon: <PublicIcon />, active: false , path: "/south-asia"},
      { id: "South East Asia", icon: <PublicIcon />, active: false , path: "/south-east-asia"},
      { id: "Australia and New Zealand", icon: <PublicIcon />, active: false , path: "/australia-and-new-zealand"},
    ],
  },
  {
    id: "About",
    children: [
      { id: "Theam", icon: "" },
      { id: "Handbook", icon: "" },
    ],
  },
];

const styles = (theme) => ({
  categoryHeader: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
  categoryHeaderPrimary: {
    color: theme.palette.common.white,
  },
  item: {
    paddingTop: 1,
    paddingBottom: 1,
    color: "rgba(255, 255, 255, 0.7)",
    "&:hover,&:focus": {
      backgroundColor: "rgba(255, 255, 255, 0.08)",
    },
  },
  itemCategory: {
    backgroundColor: "#232f3e",
    boxShadow: "0 -1px 0 #404854 inset",
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
  firebase: {
    fontSize: 24,
    color: theme.palette.common.white,
  },
  itemActiveItem: {
    color: "#ff4d4d",
  },
  itemPrimary: {
    fontSize: "inherit",
  },
  itemIcon: {
    minWidth: "auto",
    marginRight: theme.spacing(2),
  },
  divider: {
    marginTop: theme.spacing(2),
  },
  navLink: {
    textDecoration: "none",
  },
});

function Navigator(props) {
  const { classes, ...other } = props;

  return (
    <Drawer variant="permanent" {...other}>
      <List disablePadding>
        {categories.map(({ id, children }) => (
          <React.Fragment key={id}>
            <ListItem className={classes.categoryHeader}>
              <ListItemText
                classes={{
                  primary: classes.categoryHeaderPrimary,
                }}
              >
                {id}
              </ListItemText>
            </ListItem>
            {children.map(({ id: childId, icon, active, path }) => (
              <Link className={classes.navLink} key={childId} to={path} >
                <ListItem
                  key={childId}
                  button
                  className={clsx(
                    classes.item,
                    active && classes.itemActiveItem
                  )}
                >
                  <ListItemIcon className={classes.itemIcon}>
                    {icon}
                  </ListItemIcon>
                  <ListItemText
                    classes={{
                      primary: classes.itemPrimary,
                    }}
                  >
                    {childId}
                  </ListItemText>
                </ListItem>
              </Link>
            ))}

            <Divider className={classes.divider} />
          </React.Fragment>
        ))}
      </List>
    </Drawer>
  );
}

Navigator.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Navigator);
