import {
  withScriptjs,
  withGoogleMap,
  GoogleMap,
  Marker,
  KmlLayer,
} from "react-google-maps";

const MapKML = withScriptjs(
  withGoogleMap((props) => (
    <GoogleMap
      defaultZoom={8}
      defaultCenter={{ lat: props.data.lat, lng: props.data.lng }}
    >
      <KmlLayer
        url= {props.data.file}
        options={{ preserveViewport: false }}
      />
      {props.isMarkerShown && (
        <Marker/>
      )}
    </GoogleMap>
  ))
);

const SouthEastAsia = (props) => {
  
  const { loc } = props;

  return (
    <MapKML
      isMarkerShown
      googleMapURL="https://maps.googleapis.com/maps/api/js?key=AIzaSyBMLe5XT2ZEIAJ0mHe6AXeP0Mg-PBHPQto"
      loadingElement={<div style={{ height: `100%` }} />}
      containerElement={<div style={{ height: `80vh` }} />}
      mapElement={<div style={{ height: `100%` }} />}
      data = {datas.loc["pin"]}
    />
  );
};

const datas = {
  loc: {
    pin: {
      file:
        "https://firms.modaps.eosdis.nasa.gov/data/active_fire/c6/kml/MODIS_C6_SouthEast_Asia_24h.kml",
      lat:  45.429,
      lng: -94.020,
    },
  },
};

export default SouthEastAsia;
